header {
  position: fixed;
  z-index: 3;
  top: 0;
  left: 0;
  right: 0;
  height: 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 30px;
  background: transparent;
}

.header-logo {
  height: 30px;
}

.header-logo img {
  height: 100%;
}

.header-user {
  height: 35px;
}

.header-user img {
  height: 100%;
  border-radius: 3px;
}
